.footer {
  background-color: $header-footer-color;
}

.footer-fist-div {
  position: relative;
  display: flex;
  justify-content: center;

  padding-top: 115px;
  padding-bottom: 43px;
}

.footer-fist-div::after {
  content: '';
  display: block;

  position: absolute;
  bottom: 0;

  background-color: $primary-text-color;
  border-radius: 1px;

  width: 1000px;
  height: 1px;
}

.footer-list-div {
  display: flex;
}

.footer-list .footer-item + .footer-item {
  margin-left: 81px;
}

.footer-link {
  font-size: 14px;
  line-height: 1.43;
  font-weight: bold;
}

.footer-link:hover,
.footer-link:focus {
  color: $accent-color;
}

.footer-list .footer-item-link + .footer-item-link {
  margin-left: 54px;
}

.footer-list {
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer-logo-link {
  margin-left: 81px;
  margin-right: 81px;

  font-size: 18px;
  line-height: 2.17;
  font-family: Homemade Apple;

  color: $accent-color;
}

.footer-social {
  display: flex;
  justify-content: center;

  padding-top: 55px;
  margin-bottom: 73px;
}

.footer-social-item {
  background-color: $primary-text-color;
  width: 38px;
  height: 38px;

  border-radius: 50%;
}

.footer-social .footer-social-item + .footer-social-item {
  margin-left: 26px;
}

.footer-info-list {
  display: flex;
  justify-content: center;

  padding-bottom: 35px;

  line-height: 1.43;
}

.footer-info-list .footer-info-item + .footer-info-item {
  margin-left: 14px;
}
